import {useRef} from 'react';
import '../Styles/mainpg.scss';
import Projects from './Projects';
import {profile, project} from './data';

const MainPage = () => {
  const ref = useRef(null);
  const scroll = () => ref.current.scrollIntoView();
  // console.log(project)

  return(
    <>
      <div className='mainpg-container'>
        <section id='profile-container'>
          <div className='descriptors'>
            <h1 className='name'>{profile.name}</h1>
            <p className='description'>{profile.description}</p>
            <p className='about'>{profile.about}</p>
            {/* <p>{profile.connect}</p>
            <p>{profile.connect}</p> */}
            <button onClick={scroll}>View Projects</button>
          </div>
        </section>
      </div>

      <h1 id='refh'ref={ref}>Projects:</h1>
      <section id='prj-section'>
        {
          project && 
            project.map(project => <Projects key={'project-' + project.id} projects={project} />)
        }
      </section>
    </>
  );
}

export default MainPage;