import React from 'react';
import '../Styles/projects.scss';


export default function Projects(props) {
  const project = props.projects
  return (
  <>
    <section id='project-container'>
      {/* not sure just worked better in layout like this  */}
      <img className='prjt-img'src={project.image} alt='project preview'/>
      <h1 className='overlay_title'>{project.prname}</h1>
      <div className='project_descriptors'> 
        <a href={project.p_link} alt='project link' target='_blank' rel='noreferrer noopener' className='overlay_btn'>View Project</a>
        <a href={project.github} alt='github link' target='_blank' rel='noreferrer noopener' className='overlay_btn'>Github</a>
      </div>
      {/* this is separated for the :hover overlay */}
      <div className='overlay'>
        <p className='overlay_p'>{project.description}</p>
      </div>
    </section>
  </>
  )
};
